import Headers from '../Components/MainPage/Headers';
import Footer from '../Components/MainPage/Footer';
import '../Css/PageCss/NewsArticlePage.css';
import {Link} from "react-router-dom"


const NewsArticle = ({ArticleTypes, LinkImg}) => {
    return (
        <>
            <Headers Page={"N"}/>
            <div className="ArticleContaine">
                <div className="MainImage">
                    <img className="ImageArticle" src={"https://dtlaca8pgqtx.cloudfront.net/post/44fdf4ed-8de6-4879-94de-bde3d0cbed33.jpeg"} alt=""></img>
                    <h1>Bienvenue <span style={{color:"#02A3CF"}}>Cabochard</span></h1>
                    <h2>Lucas « Cabochard » Simon-Meslet rejoint la Karmine Corp en tant que toplaner de l'équipe LFL</h2>
                </div>
                <div className="LineArtcile">
                    
                    <div className="ImgLine">
                        <img src={"https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/8/citation-3.jpg"} alt="" style={{width:"100%"}}/>
                    </div>
                    <div className="InnerLine">
                        <p className="ParagrapheText" > <span>Paris, 29 mai 2021 -</span> Après avoir remporté les championnats de France et d’Europe de League of Legends, la Karmine Corp a décidé de procéder à des changements d'équipe suite au départ du toplaner Adam « Adam » Maanane. Il sera remplacé en toplane par un véritable vétéran de la scène compétitive, <span>Lucas « Cabochard » Simon-Meslet</span>, qui a participé à la ligue majeure européenne - LCS EU puis LEC - pendant six ans au sein de Gambit Gaming et de Team Vitality, et a même participé au championnat du monde en 2018. Son expérience sera un atout de poids pour la Karmine Corp, prête à  défendre son titre de meilleure équipe française et européenne lors du Summer Split.</p>
                        <h1>Karmine Corp</h1>
                    </div>
                </div>
                <div className="LineArtcileDeux">
                    <p className="ParagrapheText" >Cabochard est réputé parmi les toplaners d'Europe comme étant l'un des plus agressifs à son rôle, tout en proposant de la flexibilité et en étant capable d'assumer un rôle de leader en jeu. « Après un split de pause, je suis ravi de relever un nouveau défi aux côtés de la Karmine Corp. J'ai hâte de jouer avec mes nouveaux coéquipiers et de remporter la LFL et les EU Masters. », ajoute Cabochard. Vous pourrez retrouver Cabochard dès ce soir pour son premier stream sous les couleurs de la Karmine Corp. Accueillez-le comme il se doit ! <span>Cabane123</span></p>
                    <div className="ImgLine">
                        <img src={"https://pbs.twimg.com/media/E3dwdFgXIAUndA9?format=jpg&name=large"} alt="" style={{width:"100%"}}/>
                    </div>
                </div>
                <div className="LastLine">
                    <Link to="/News" style={{width:"max-content"}} className="LinkBtn" > <button className="BtnBlue">{'<'} News Page</button> </Link>
                    <p>Karmine Corp<br/><a href="https://twitter.com/KarmineCorp">https://twitter.com/KarmineCorp</a><br/><a href="https://twitter.com/CabochardLoL">https://twitter.com/CabochardLoL</a></p>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default NewsArticle
