import { Link } from "react-router-dom"

const About = ({Text}) => {
    return (
        <div className="AboutComponent">
            <div className="ContaineTitleAbout">
                <h1><span>K</span>armine <span>C</span>orp</h1>
                <div className="ParagrapheAbout">
                    <p>{Text}</p>
                    <Link to="/About" className="ButtonAbout"><span>View About {'>'}</span></Link>
                </div>
            </div>
        </div>
    )
}

export default About
