import '../../Css/Headers.css'
import IconMaillots from '../../Image/Headers/MaillotsIcon.svg'
import KarmineLogo from '../../Image/KarmineLogo.png'
import NavBar from '../NavBar'
import {useState, useEffect} from 'react'
import {Link} from "react-router-dom";

const Headers = ({Page, Scroll, lienBck, ArraySponso}) => {
    const [NavActive, setNavActive] = useState(false);
    const [count, setcount] = useState(0);
    const [count1, setcount1] = useState(1);

    useEffect(() => {
        const interval = setInterval(() => {
            setcount((count+1) %(ArraySponso.length));
            setcount1((count1+1) %(ArraySponso.length));
        }, 13000);
        return () => clearInterval(interval);
      }, [count, count1, ArraySponso]);

    return (
        <>
            <div className="Headers" style={{backgroundColor : NavActive ? "rgba(255, 255, 255, .0)" : Scroll === true && "rgba(255, 255, 255, .0)"}}>
                <div className="ImgIconMenu">
                    <span>
                        <img onClick={()=> setNavActive(!NavActive)} className="IconMenu" alt="IconMenu" src={!NavActive ? "https://img.icons8.com/material-sharp/96/FFFFFF/menu--v3.png" : "data:image/png;base64,"}/>
                    </span>
                </div>
                <div className="HeaderButton">
                    {!NavActive &&
                        <>
                            <Link to="/Team"> <h1 style={{opacity: Page==="T" && "1"}}><span>T</span>eams</h1> </Link>
                            <Link to="/News"><h1 style={{opacity: Page==="N" && "1"}}><span>N</span>ews</h1></Link>
                        </> 
                    }
                </div>
                <div className="LogoContaine">
                    {ArraySponso[count] !== undefined && 
                    <img className="LogoSponsort1 Sponsort" alt="LogoSponsort" src={lienBck + `/Image/About/${ArraySponso[count].Img}?${count}`}/>}
                    <Link to="/Main" className="MainBAck"><img className="KarmineLogo" alt="KarmineLogo" src={KarmineLogo}/></Link>
                    {ArraySponso[count1] !== undefined && 
                    <img className="LogoSponsort2 Sponsort" alt="LogoSponsort" src={lienBck + `/Image/About/${ArraySponso[count1].Img}?${count1}`}/>}
                </div>
                <div className="HeaderButton">
                    {!NavActive &&
                        <>
                            <Link to="/Match"> <h1 style={{opacity: Page==="M" && "1"}}><span>M</span>atch</h1> </Link>
                            <h1><span>S</span>hop</h1>
                        </> 
                    }
                </div>
                <img className="IconMaillots" alt="IconMaillots" src={IconMaillots}/>
            </div>
            {NavActive && <NavBar/>}
        </>
    )
}

export default Headers
