import Headers from '../Components/MainPage/Headers'
import Footer from '../Components/MainPage/Footer'
import '../Css/PageCss/ContactPage.css'
import KcLogo from '../Image/Contact/logoContact.png'

const ContactPage = ({lienBck, ArraySponso}) => {
    return (
        <>
            <Headers lienBck={lienBck} ArraySponso={ArraySponso}/>
            <div className="ContactContain">
                <div className="ContactCard">
                    <form className="ContactCard1">
                        <h1><span>C</span>ontact <span>U</span>s</h1>
                        <input type="text" required placeholder={"Nom"} minLength="3"></input>
                        <input type="email" id="email" pattern=".+@gmail\.com" size="30" required placeholder={"E-mail"}></input>
                        <textarea type="text" placeholder="Message" required minLength="30" ></textarea>
                        <button>Envoier</button>
                    </form>
                    <div className="ContactCard2">
                        <div className="ContainTextMoving">
                            <div className="ImgContainContact">
                                <img src={KcLogo} alt=""/>
                            </div>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <div className="MargineKc">
                                <h1>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h1>
                                <h1>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h1>
                                <h1>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h1>
                            </div>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                            <h2>#KCORP #KCORP #KCORP #KCORP #KCORP #KCORP</h2>
                        </div>
                    </div>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default ContactPage
