const Match = ({match, lienBck}) => {

    function returnDate(isoDate){
        let date = new Date(isoDate);
        return date.getDate() + ' '+ date.toLocaleString('default', { month: 'long' }) + ', ' + date.getFullYear();
    };
    function formatString(string){
        return string.split(' ').join('_').split('.').join("_").split("-").join("_").split('/').join('_').replace(/[^\x20-\x7E]+/g, '');
    };

    return (
        <div className="Match Colone" >
            <div className="TopContainMatch" >
                <div style={{display:"flex", alignItems:"flex-end"}}>
                    <img alt="LogoJeux" className="LogoJeux" src={lienBck + `/Image/Match/${formatString(match.Game)}`}/>
                    <h2>{returnDate(match.Date)}</h2>
                </div>
                <div style={{display:"flex", alignItems:"flex-end"}}>
                    <img alt="LogoCompete" className="LogoTournament" src={lienBck + `/Image/Match/${formatString(match.Tournament)}`}/>
                </div>
            </div>
            <div className="BottomContainMatch">
            {
                (new Date(match.Date).getHours() !== 0 || new Date(match.Date).getMinutes() !==0 ) ?
                <div className="Colone">
                    <h3 style={{margin:"0"}}>{new Date(match.Date).getHours() +"H" +new Date(match.Date).getMinutes()}</h3>
                    <h4 style={{margin:"0"}}>UTC+2</h4>
                </div> :
                <div className="Colone">
                    <h3 style={{margin:"0"}}>{typeof match.Score[0] === "string" ? "FFA" : `BO${match.Score[0].Team + match.Score[0].Opponent}`}</h3>
                    <h4 style={{margin:"0", color : match.Victory ? "#118C54" : "#BE1F17"}} >{match.Victory ? "win" : "lose"}</h4>
                </div>
                }
                <div style={{display:"flex", width : "100%", justifyContent : "center", alignItems : "center"}}>
                    <img className="LogoMatch" alt="LogoKc" src={lienBck + '/Image/Match/Karmine'}/>
                    {match.Score !== undefined &&  <h5 className="ScoreMatch" style={{color : match.Victory ? "#118C54" : "#BE1F17"}} >{(typeof match.Score[0] !== "string") ? match.Score[0].Team : match.Score[0]}</h5>}
                    {(match.Opponent !== undefined && (match.Opponent !== "")) && <h2 style={{fontSize : "1.4em"}}>vs</h2> }
                    {((match.Score !== undefined) && (typeof match.Score[0] !== "string")) && <h5 className="ScoreMatch" style={{color : match.Victory ? "#118C54" : "#BE1F17"}} >{match.Score[0].Opponent}</h5>}
                    {((match.Opponent !== undefined) && (match.Opponent !== "")) && <img className="LogoMatch" alt="LogoAdversaire" src={(lienBck + '/Image/Match/'+ formatString(match.Opponent))}/>}
                </div>
                <a className="Btn" href={match.Data}>Watch <span style={{fontSize : "1.5vh"}}>{'>'}</span></a>
            </div>
        </div>
    )
}

export default Match
