import {  BrowserRouter as Router, Switch, Route, Redirect} from "react-router-dom";
import { useState, useEffect } from "react";
import Main from './Pages/Main'
import NewsPage from './Pages/NewsPage'
import MatchPage from './Pages/MatchPage'
import TeamPage from './Pages/TeamsPage'
import Article from './Pages/NewsArticle'
import AboutPage from './Pages/AboutPage'
import ContactPage from './Pages/ContactPage'
import WaintingScreen from "./Pages/WaitingScreen";

import GamePage from './Pages/GamePage'


// import BrandPage from "./Pages/BrandPage";


function App() {
  const [lienBck] = useState('');
  const [ArraySponso, setArraySponso] = useState([]);
  const [ArrayText, setArrayText] = useState([]);
  const [PastMatch, setPastMatch] = useState([]);
  const [PastMatchHistorie, setPastMatchHistorie] = useState([]);
  const [FutureMatch, setFutureMatch] = useState([]);
  const [ArrayImg, setArrayImg] = useState([]);
  const [IsLoad, setIsLoad] = useState(false);

  const [LineUpLol, setLineUpLol] = useState([]);
  const [LineUpTm, setLineUpTm] = useState([]);
  const [LineUpTft, setLineUpTft] = useState([]);
  const [LineUpRl, setLineUpRl] = useState([]);

  /*Get Data with that*/
    useEffect(() => {
      function GetSponso()
      {
        return fetch(lienBck  + "/Data/About", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setArraySponso(array))
      };
      function GetText()
      {
        return fetch(lienBck  + "/Data/Main/Main/", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setArrayText(array))
      };
      function GetListItem()
      {
        return fetch(lienBck  + "/Data/Match/Main/Past/", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setPastMatch(array))
      };
      function GetListMath()
      {
        return fetch(lienBck  + "/Data/Match/Main/Future", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setFutureMatch(array))
      };
      function GetImg()
      {
        return fetch(lienBck  + "/Data/Shop", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setArrayImg(array))
      };
      function GetMatchPast()
      {
        return fetch(lienBck  + "/Data/Match/Main/PastHistorie", {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setPastMatchHistorie(array))
      };
      function GetLineUp(Game, setLineup)
      {
        Game = Game.replace(' ', '_');
        return fetch(lienBck  + `/Data/Team/LineUp/${Game}`, {method: "GET", headers: {'Accept': 'application/json, text/plain, */*','Content-Type': 'application/json'}})
        .then(res => {return res.json()})
        .then(array => setLineup(array))
      };
      if (IsLoad !== true){      
        GetImg();
        GetListItem();
        GetListMath();
        GetText();
        GetSponso();
        GetMatchPast();
        GetLineUp('league of legend', setLineUpLol);
        GetLineUp('Trackmania', setLineUpTm);
        GetLineUp('Tft', setLineUpTft);
        GetLineUp('Rocket league', setLineUpRl);
      }
      (ArrayImg[0] !== undefined && ArrayText[0] !== undefined && PastMatch[0] !== undefined && ArraySponso[0] !== undefined && PastMatchHistorie[0] !== undefined && LineUpLol[0] !== undefined && LineUpTm[0] !== undefined && LineUpTft[0] !== undefined && LineUpRl[0] !== undefined) && setIsLoad(true);
    }, [lienBck, ArrayImg, ArrayText, PastMatch, ArraySponso, PastMatchHistorie, IsLoad, LineUpLol, LineUpTm, LineUpRl, LineUpTft])

  
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          {IsLoad !== false ? <Redirect to="/Main"/> : <WaintingScreen/>}
        </Route>
        <Route exact path="/Main">
          <Main lienBck={lienBck} ArrayImg={ArrayImg} FutureMatch={FutureMatch} PastMatch={PastMatch} ArrayText={ArrayText} ArraySponso={ArraySponso}/>
        </Route> 
        <Route exact path="/News" >
          <NewsPage lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route>
        <Route exact path="/Match" >
          <MatchPage lienBck={lienBck} ArraySponso={ArraySponso} FutureMatch={FutureMatch} PastMatch={PastMatch} PastMatchHistorie={PastMatchHistorie}/>
        </Route>
        <Route exact path="/Team">
          <TeamPage lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route>
        <Route exact path="/News/Article">
          <Article lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route>
        <Route exact path="/About">
          <AboutPage lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route>
        {/* <Route exact path="/Brand">
          <BrandPage lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route> */}
        <Route exact path="/Contact">
          <ContactPage lienBck={lienBck} ArraySponso={ArraySponso}/>
        </Route>

        <Route exact path="/Team/Lol">
         {IsLoad === false || IsLoad === undefined  ? <WaintingScreen/> : <GamePage lienBck={lienBck} ArraySponso={ArraySponso} LineUp={LineUpLol} Game={'league of legend'} Bck={"https://www.actugaming.net/wp-content/uploads/2016/06/league-of-legends.jpg"}/>}
        </Route>
        <Route exact path="/Team/Tm">
         {IsLoad === false || IsLoad === undefined ? <WaintingScreen/> : <GamePage lienBck={lienBck} ArraySponso={ArraySponso} LineUp={LineUpTm} Game={'Trackmania'} Bck={"https://global-img.gamergen.com/trackmania-screenshot-review-11_0000956715.jpg"}/>}
        </Route>
        <Route exact path="/Team/RL">
         {IsLoad === false || IsLoad === undefined ? <WaintingScreen/> : <GamePage lienBck={lienBck} ArraySponso={ArraySponso} LineUp={LineUpRl} Game={'Rocket league'} Bck={"https://coolthemestores.com/wp-content/uploads/2021/07/rocket-league-wallpaper-background-1.jpg"}/>}
        </Route>
        <Route exact path="/Team/Tft">
         {IsLoad === false || IsLoad === undefined ? <WaintingScreen/> : <GamePage lienBck={lienBck} ArraySponso={ArraySponso} LineUp={LineUpTft} Game={'Tft'} Bck={"https://wallpapercave.com/wp/wp7407642.jpg"}/>}
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
