import WhiteFigure from '../../Image/News/Figure.svg'


const NewsMinature = ({Title, SubTitle, SrcImage, UrlArticle}) => {
    return (
        <div className="DIvNews">
            <div>
                <div className="content" >
                    <img src={WhiteFigure} className="FigureMiniature" alt="" />
                    <p>
                        <img src={SrcImage} className="ImgNews" alt="" />
                    </p>
                    <img src={WhiteFigure} className="FigureMiniatureReverse" alt="" /> 
                </div>
                <div className="TitreNewsPage">
                    <h1 >{Title}</h1>
                    {SubTitle !== "" && <h2>{SubTitle}</h2>} 
                </div>
            </div>
        </div>
    )
}

export default NewsMinature