import {useState, useEffect} from 'react'

const Shop = ({Text, lienBck, ArrayImg}) => {
    const [count, setcount] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setcount(count+1);
        }, 10000);
        return () => clearInterval(interval);
      }, [count]);


    return (
        <div className="ShopComponent Colone">
            <div className="ContaineShop">
                <div className="ContaineTextShop reveal revealLeft" dangerouslySetInnerHTML={{ __html: Text }} />
                <div className="ContaineShopImg reveal revealRight">
                    {ArrayImg[count % (ArrayImg.length-1)] !== undefined && 
                    <div className="ImgContaine">
                        <img style={{width : "70%"}} alt="" src={lienBck + `/Image/Shop/${ArrayImg[count % (ArrayImg.length-1)].Img}`} className="SecondeImg"/>
                        <img style={{width : "90%"}} alt="" src={lienBck + `/Image/Shop/${ArrayImg[(count+1) % (ArrayImg.length-1)].Img}`} className="SecondeImg"/>
                        <img style={{width : "70%"}} alt="" src={lienBck + `/Image/Shop/${ArrayImg[(count+2) % (ArrayImg.length - 1)].Img}`} className="SecondeImg"/>
                    </div>
                    }
                </div>
            </div>
        </div>
    )
}

export default Shop
