import {useState} from 'react'
import Headers from '../Components/MainPage/Headers'
import Footer from '../Components/MainPage/Footer'
import NewsMinature from '../Components/News/NewsMiniature'
import YoutubeMinature from '../Components/News/YoutubeMiniature'
import '../Css/PageCss/NewsPage.css'

const NewsPage = ({lienBck, ArraySponso}) => {
    const [PageNews, setPageNews] = useState(1);
    const [PageVideo, setPageVideo] = useState(1);

    const DataNews = [{Title : "Title", SubTitle: "SubTitle", Miniature: "https://pbs.twimg.com/media/E1M0o4tX0AI2sd4?format=jpg&name=medium"},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://pbs.twimg.com/media/E1MzmEGXsAEgjwS?format=jpg&name=medium", Link : ""},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/2/KARMINE_CORP_REJOINS_LA_LFL.jpg", Link : ""},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://pbs.twimg.com/media/E0aHD5BWUAIK4k2?format=jpg&name=large", Link : ""},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/1/KARMINE_CORP_x_MSI.jpg", Link : ""},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://dtlaca8pgqtx.cloudfront.net/post/afe622d4-5e66-4fe8-af7c-b8c856d9cb7d.jpg", Link : ""},
    {Title : "Title", SubTitle: "SubTitle", Miniature: "https://dtlaca8pgqtx.cloudfront.net/post/44fdf4ed-8de6-4879-94de-bde3d0cbed33.jpeg", Link : ""}]

    const DataYoutube = [{Title : "ON PEUT FAIRE UN RECORD DU MONDE !", SubTitle : "KCORP EUMASTERS Voice Comms #12", ImgMinia : "https://img.youtube.com/vi/CROsr1fSX3s/maxresdefault.jpg"},
    {Title : "ON PEUT FAIRE UN RECORD DU MONDE !", SubTitle : "KCORP EUMASTERS Voice Comms #12", ImgMinia : "https://img.youtube.com/vi/-hkDfyleCFM/maxresdefault.jpg", Link : ""},
    {Title : "Selena Gomez ou Angèle ? Diamant 1 ou Challenger ?", SubTitle : "BEP BEEP CINKROF", ImgMinia : "https://img.youtube.com/vi/5KPE5bTwVhs/maxresdefault.jpg", Link : ""},
    {Title : "J'ARRIVE PAS A Y CROIRE, ON A GAGNÉ !!!", SubTitle : "KCORP EUMASTERS Voice Comms #14", ImgMinia : "https://img.youtube.com/vi/OmmeUuSyDrE/maxresdefault.jpg", Link : ""},
    {Title : "JE SUIS SI HEUREUX, JY CROIS PAS !", SubTitle : "KCORP EUMASTERS Voice Comms #10", ImgMinia : "https://img.youtube.com/vi/eFBsFgqPypg/maxresdefault.jpg", Link : ""},
    {Title : "FAQ à grande vitesse avec Kameto & Prime", SubTitle : "", ImgMinia : "https://img.youtube.com/vi/waMBbBxLBCY/maxresdefault.jpg", Link : ""},
    {Title : "ON PEUT FAIRE UN RECORD DU MONDE !", SubTitle : "KCORP EUMASTERS Voice Comms #12", ImgMinia : "https://img.youtube.com/vi/1fcukNTfZxY/maxresdefault.jpg", Link : ""},
    {Title : "Trayton ou Karnage ? GoT ou Vikings ?", SubTitle : "BEP BEEP CABOCHARD", ImgMinia : "https://img.youtube.com/vi/P0QuEF9z1xc/maxresdefault.jpg", Link : ""}]

    function DataTraitement(Data){
        let NewData = [];
        while (Data.length){
            NewData.push(Data.splice(0,6));
        }
        return NewData;
    }

    const ShowMinia = (Data)=>{
        let Liste = [];
        for (const element of Data){
            Liste.push(<NewsMinature Title={element.Title} SubTitle={element.SubTitle} SrcImage={element.Miniature} key={Data.indexOf(element)} />)
        };
        return Liste;
    }
    const ShowMiniaY = (Data)=>{
        let Liste = [];
        for (const element of Data){
            Liste.push(<YoutubeMinature Title={element.Title} SubTitle={element.SubTitle} ImgMinia={element.ImgMinia} key={Data.indexOf(element)}/>)
        };
        return Liste;
    }

    const DataN = DataTraitement(DataNews);
    const DataY = DataTraitement(DataYoutube);

    return (
        <>
            <Headers Page={"N"} lienBck={lienBck} ArraySponso={ArraySponso}/>
            <div className="ContentContain">
                <h1 className="TitleContain">News</h1>
                <div className="ContainListe">
                    {
                        ShowMinia(DataN[PageNews-1])
                    }
                </div>
                <h1 className="ChangersPage">{PageNews!==1 && <span onClick={()=> setPageNews(PageNews - 1)}>{'<'}</span>} {PageNews}/{DataN.length} {PageNews !== DataN.length && <span onClick={()=> setPageNews(PageNews + 1)}>{'>'}</span>}</h1>
                <h1 className="SubTitleContain">Karmine</h1>
            </div>
            <div className="ContentContain" style={{backgroundColor :"#FFFFFF", paddingTop : "0 !important"}}>
                <div className="ContainListe" style={{marginTop :"4%"}}>
                    {
                        ShowMiniaY(DataY[PageVideo-1])
                    }
                </div>
                <h1 className="ChangersPage" style={{color : "#000000"}}>{PageVideo!==1 && <span onClick={()=> setPageVideo(PageVideo - 1)} style={{color : "#000000"}}>{'<'}</span>} {PageVideo}/{DataY.length} {PageVideo !== DataY.length && <span onClick={()=> setPageVideo(PageVideo + 1)} style={{color : "#000000"}}>{'>'}</span>}</h1>
            </div>
            <Footer/>
        </>
    )
}

export default NewsPage