import Headers from '../Components/MainPage/Headers'
import Footer from '../Components/MainPage/Footer'
import '../Css/PageCss/MatchPage.css'
import Match from '../Components/MainPage/Match'
import RechercheMatch from '../Components/Match/RechercheMatch'
import { useEffect, useState } from "react";

const MatchPage = ({lienBck, ArraySponso, FutureMatch, PastMatch, PastMatchHistorie}) => {
    let NextMatch
    if (FutureMatch[0]===undefined){
        NextMatch =  new Date().toISOString()
    }
    else{
        NextMatch = FutureMatch[0].Date
    }
    const NextMatchTime = new Date(NextMatch);
    const [timeLeft, setTimeLeft] = useState({});

    useEffect(() => {
        const timer=setTimeout(() => {
          setTimeLeft(ConvertionDate(NextMatchTime - Date.now()) );
        }, 1000);
        // Clear timeout if the component is unmounted
        return () => clearTimeout(timer);
      });

    function ConvertionDate(miliseconde){
        return {
            seconde : parseInt((miliseconde/1000)%60),
            minute : parseInt((miliseconde/(1000*60))%60),
            heure : parseInt((miliseconde/(1000*60*60))%60),
            jour : parseInt((miliseconde/(1000*60*60*24))),
        }
    };

    return (
        <>
            <Headers Page={"M"} lienBck={lienBck} ArraySponso={ArraySponso}/>
            <div className="MatchContent">
                <div className="Countdown">
                    <h1><span>N</span>ext <span>M</span>atch :</h1>
                    {
                        NextMatchTime - Date.now() > 0 ?
                        <div className="CountDownContain" ><div><span>{timeLeft.jour}</span><h2>Day</h2></div><i></i><div><span>{timeLeft.heure}</span><h2>Heure</h2></div><i></i><div><span>{timeLeft.minute}</span><h2>Minute</h2></div><i></i><div><span>{timeLeft.seconde}</span><h2>Seconde</h2></div></div>
                        :
                        <h1 className="TwitchChannelMatch">twitch.tv/<span>kamet0</span></h1>
                    }
                </div>
                <div className="MatchContaine">
                    <div className="ColoneNextMatch Colone">
                        <h1 className="Text" style={{fontSize:"3.2vh", color : "#FFFFFF", alignSelf :"flex-end", marginBottom:"1.4vh"}}><span style={{color: "#02A3CF"}}>U</span>pcomming Matchs</h1>
                        <div className="ColoneMatchs">
                            {
                                FutureMatch.map((element)=>{
                                    return <Match key={element._id} match={element} lienBck={lienBck}/>
                                })
                            }
                        </div>
                    </div>
                    <div className="ColoneLastMatch Colone">
                        <h1 className="Text" style={{fontSize:"3.2vh", color : "#FFFFFF", alignSelf:"flex-start", marginBottom:"1.4vh"}}><span style={{color: "#02A3CF"}}>L</span>ast Matchs</h1>
                        <div className="ColoneMatchs">
                            {
                                PastMatch.map((element)=>{
                                    return <Match key={element._id} match={element} lienBck={lienBck}/>
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div className="HitsoriqueMatch">
                <div className="HistoriqueMatchConayin">
                    <div className="MatchContaine HistoriqueMatchFind">
                        <div className="ColoneNextMatch Colone">
                            <h1 className="Text" style={{fontSize:"3.2vh", color : "#FFFFFF", alignSelf :"flex-end", marginBottom:"1.4vh"}}><span style={{color: "#02A3CF"}}>L</span>ast Matchs</h1>
                            <div className="ColoneMatchs">
                                {
                                    PastMatchHistorie.map((element)=>{
                                        return <Match key={element._id} match={element} lienBck={lienBck}/>
                                    })
                                }
                            </div>
                        </div>
                        <RechercheMatch/>
                        <div className="TrophyStyleMatch">
                            <div className="BlueWall">
                                <h1>TFT World Champion <span>X<i>1</i></span></h1>
                                <h1>LFL CHampion <span>X<i>1</i></span></h1>
                                <h1>EUM Champion <span>X<i>1</i></span></h1>
                            </div>
                            <div className="GifContain">
                                <img className="GifMatch" src={"https://f.hellowork.com/blogdumoderateur/2013/02/nyan-cat-gif-1.gif"} alt=""/>
                                <h1>@createur</h1>
                                <h2>Gif of the week</h2>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default MatchPage
