import YoutubeLogo from '../../Image/News/YoutubeLogo.svg'
import KarmineLogoNoir from '../../Image/News/LogoKarmineNoir.svg'

const YoutubeMinature = (props) => {
    return (
        <div className="DIvNews">
            <div className="YoutubeMinia">
                <h1>{props.Title}</h1>
                <div style={{position :"relative", maxWidth :"354px"}}>
                    <img src={YoutubeLogo} alt="" className="FigureYoutube" ></img>
                    <img src={props.ImgMinia} alt="" className="ImgNews" ></img>
                    <img src={KarmineLogoNoir} alt="" className="FigureLogo" ></img>
                </div>
                <h2>{props.SubTitle}</h2>
            </div>   
        </div>
    )
}

export default YoutubeMinature