import Match from '../MainPage/Match'
import EmoteFumi from '../../Image/Match/EmoteFumi'
import EmoteFlag from '../../Image/Match/EmoteFlag'
import EmoteEum from '../../Image/Match/EmoteEum'
import EmoteLogo from '../../Image/Match/EmoteLogo'
const Matchs = ({lienBck, PastMatch, FutureMatch}) => {
    return (
        <div className="MatchComponent Colone">
            <h1 className="MatchCompTitle"><span>W</span>atch <span>U</span>s</h1>
            <div className="StyleMatchComp">
            </div>
            <div className="MatchComp">
                <div className="RsDiv reveal revealLeft">
                    <img alt="Emote" src={EmoteFumi} style={{top:"7%", left:"80%", transform:"rotate(10deg)"}}/>
                    <img alt="Emote" src={EmoteEum} style={{bottom:"15%", left:"2%", transform:"rotate(-10deg)"}}/>
                    <img alt="Emote" src={EmoteFlag} style={{top:"7%", left:"0%", transform:"rotate(-5deg)"}}/>
                    <img alt="Emote" src={EmoteLogo} style={{bottom:"15%", right:"0%", transform:"rotate(5deg)"}}/>
                    <h1 className="TExtRsDiv"><span>R</span>éagiser sur les <span>R</span>éseaux <span>!</span><br></br><span style={{fontSize:"3.7vh",margin:"0",color:"#2B2933"}}>#KCROP</span></h1>
                </div>
                <div className="MatchContaine">
                    <div className="ColoneNextMatch Colone reveal revealLeft">
                        <h1 className="Text" style={{fontSize:"3.2vh", color : "#383D47", alignSelf :"flex-end", marginBottom:"1.4vh"}}>Upcomming Matchs</h1>
                        <div className="ColoneMatchs">
                            {
                                FutureMatch.map((element)=>{
                                    return <Match key={element._id} match={element} lienBck={lienBck}/>
                                })
                            }
                        </div>
                    </div>
                    <div className="ColoneLastMatch Colone reveal revealRight">
                        <h1 className="Text" style={{fontSize:"3.2vh", color : "#383D47", alignSelf:"flex-start", marginBottom:"1.4vh"}}>Last Matchs</h1>
                        <div className="ColoneMatchs">
                            {
                                PastMatch.map((element)=>{
                                    return <Match key={element._id} match={element} lienBck={lienBck}/>
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <div className="reveal WritingAnime">#KCORP #KCORPWIN #KCORP #KCORPWIN</div>
            </div>
        </div>
    )
}

export default Matchs
