import Carousel from  '../News/Carousel'
import { Link } from 'react-router-dom'
import KarmineLogoBlack from '../../Image/KarmineLogo.png'
const News = ({Text}) => {
    return (
        <div className="NewsComponent Colone">
            <h1 className="NewsTitleComp reveal revealLeft"><span>N</span>ews</h1>
            <div className="StyleNewsComp">
                <i className="StyleNewsBar1"></i>
                <i className="StyleNewsBar2"></i>
            </div>
            <div className="NewsForm" >
                <Carousel/>
                <div className="ParagrapheNews Colone reveal revealRight">
                    <div className="FirstLayer">
                        <img alt="BlackKarmineLogo" src={KarmineLogoBlack}/>
                        <p>{Text}</p>
                        <Link to="/News"><h1>News Page</h1></Link>
                    </div>
                    <div className="SecondLayer"></div>
                </div>
            </div>
        </div>
    )
}

export default News
