/* eslint-disable no-use-before-define */
import React from 'react';
import useAutocomplete from '@material-ui/lab/useAutocomplete';

export default function UseAutocomplete(props) {
  const {
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    options: props.Data,
    getOptionSelected : (option, value) => option.title === value.title,
    getOptionLabel: (option) => option.title,
  });

  return (
    <>
        <div className={props.InputContain} style={{display:"flex", flexDirection:"column", position : "relative"}}>
            <input onKeyDownCapture={props.fonction} placeholder={props.PlaceHolder} {...getInputProps()} style={{width: "100%"}} />
            {groupedOptions.length > 0 ? (
                <ul className={props.listbox} {...getListboxProps()} style={{width: "100%", "overflow-x" : "hidden"}}>
                {groupedOptions.map((option, index) => (
                    <li {...getOptionProps({ option, index })}>{option.title}</li>
                ))}
                </ul>
            ) : null}
        </div>
    </>
  );
}