import { useState, useEffect } from "react";
import Headers from '../Components/MainPage/Headers'
import Footer from '../Components/MainPage/Footer'
import '../Css/PageCss/TeamPage.css'
import Carte from '../Components/Team/Carte'
import UseAutocomplete from '../Components/InputAutoComplite'


const GamePage = ({lienBck, ArraySponso, LineUp, Game, Bck}) => {
    const [PageCard, setPageCard] = useState(0);
    const [SelectPlayer, setSelectPlayer] = useState('');
    const [SelectPlayerInfo, setSelectPlayerInfo] = useState({});
    const [ShowPlayerPage, setShowPlayerPage] = useState(false);
    const [ShowTextCard, setShowTextCard] = useState(false);
    const [PlayerPageType, setPlayerPageType] = useState(true);
    const [VideoState, setVideoState] = useState(false);
    const [SelectYear, setSelectYear] = useState(LineUp[0].Year);
    const [Data, setData] = useState([]);
    const size = useWindowSize();   
    let ShowCard = Math.round((size.width - 100)/360) >= 1 ? Math.round((size.width - 100)/360) : 1;
    let ListeLineUp = [];

    LineUp.forEach(element => {
        ListeLineUp.push({"title" : element.Year});
    });

    useEffect(()=>{
        const request = async()=>{
            let ekip = await fetch(lienBck  + `/Data/Team/LineUp/${Game}`);
            ekip = await ekip.json();
            let TabPlayer = ekip.find(element=>element.Year === SelectYear) !== undefined ? ekip.find(element=>element.Year === SelectYear).LineUp : ekip[0].LineUp;
            TabPlayer.forEach(async (Player) => {
                const res = await fetch(lienBck  + `/Data/Team/${Player}`);
                const json = await res.json();
                Data.push(json[0]);
            });
        };
        request();
    }, [SelectYear, Data ,lienBck, Game]);


    useEffect(() => {
        const requestPlayerInfo = async ()=>{
            const res = await fetch(lienBck  + `/Data/Team/${SelectPlayer}`);
            const json = await res.json();
            setSelectPlayerInfo(json !== undefined ?json[0] : {});
        };
        if (SelectPlayer !== ""){
            requestPlayerInfo();
        }
    }, [SelectPlayer, lienBck])

    function OnClickListenRecherche()
    {
        let value= document.querySelector(".InputTeam > input").value;
        if(value !== "" && LineUp.find((element)=> element.Year === value) !== undefined){
            setSelectYear(value);
            setSelectPlayer("");
            setData([]);
            setPageCard(0);
        };
    }

    function ChangeVideo() {
        let vid = document.getElementById("PlayerVideo");
        if (VideoState){
            vid.play();
        }else{
            vid.pause();
        }
        setVideoState(vid.paused);
    }
    
    function resetVod(){
        let vid = document.getElementById("PlayerVideo");
        vid.currentTime = 0;
    }

    function openFullscreen() {
        let vid = document.getElementById("PlayerVideo");

        if (vid.requestFullscreen) {
            vid.requestFullscreen();
        } else if (vid.webkitRequestFullscreen) { /* Safari */
            vid.webkitRequestFullscreen();
        } else if (vid.msRequestFullscreen) { /* IE11 */
            vid.msRequestFullscreen();
        }
    }
    return (
        <>
            <Headers Page={'T'} lienBck={lienBck} ArraySponso={ArraySponso}/>
            <div className="TeamsContain" id="TeamPage" style={{backgroundImage : `url(${Bck})`, backgroundRepeat : 'no-repeat', backgroundSize :'cover', backgroundPositionY : "100%"}}>
                <UseAutocomplete fonction={OnClickListenRecherche} listbox={"Autocomplite"} InputContain={"InputTeam"} PlaceHolder={"Split"} Data={ListeLineUp[0]!== undefined && ListeLineUp}/>
                <div className="CardContain">
                    <img style={{width :"50px", height :"fit-content", transform:"rotate(180deg)", cursor:"pointer"}} onClick={()=> PageCard-1 >= 0 && setPageCard(PageCard-1)} src={PageCard-1 >= 0 ? "https://img.icons8.com/ios-filled/50/FFFFFF/circled-chevron-right.png" : "https://img.icons8.com/ios-filled/50/363639/circled-chevron-right.png"} alt=""/>
                    {Data[0] !== undefined && Data.slice(ShowCard*(PageCard), ShowCard*(PageCard+1)).map(element => (
                        <Carte NumPlayer={element.Number} ImgPlayer={lienBck  + `/Image/Team/${element.Pseudo}`} LogoRole={lienBck  + `/Image/Team/${element.Role}`} Pseudo={element.Pseudo} key={Data.indexOf(element)} setPlayerFocus={setSelectPlayer} PlayerFocus={SelectPlayer} ShowTextCArdNotSelect={setShowTextCard}/>
                    ))
                    }
                    <img style={{width :"50px", height :"fit-content", cursor:"pointer"}} onClick={()=> PageCard+1 < Math.ceil(Data.length/ShowCard) && setPageCard(PageCard+1)} src={PageCard+1 < Math.ceil(Data.length/ShowCard) ? "https://img.icons8.com/ios-filled/50/FFFFFF/circled-chevron-right.png" : "https://img.icons8.com/ios-filled/50/363639/circled-chevron-right.png"} alt=""/>
                </div>
                <h1 className="TitleChoseP" style={{visibility: !ShowTextCard && "hidden"}}>Choisichez un joueur</h1>
                <a href="#PlayerPage">
                    <div className="round" onClick={()=>SelectPlayer !== '' ? setShowPlayerPage(!ShowPlayerPage) : setShowTextCard(true)} >
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </a>
            </div>
            {(ShowPlayerPage && SelectPlayerInfo !== {} && SelectPlayerInfo.Pseudo !== undefined) && 
            <div className="FitPlayerPage" id="PlayerPage">
                <div className="PlayerPage fadeInUp" >
                    <a href="#TeamPage">
                        <div className="round" onClick={()=>setShowPlayerPage(!ShowPlayerPage)} >
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                    </a>
                    <div className="PlayerPageContent" >
                        <div className="PlayerInfoContain">
                            <div className="PlayerFrame">
                                <div className="PlayerFrameImg">
                                    <img src={lienBck  + `/Image/Team/${SelectPlayerInfo.Pseudo}Prime`} alt=""/>
                                    <div></div>
                                    <h1><span>{SelectPlayerInfo !== undefined && SelectPlayerInfo.Pseudo[0]}</span>{SelectPlayerInfo.Pseudo.substr(1,SelectPlayerInfo.length)}</h1>
                                </div>
                                <h2>{SelectPlayerInfo.Name.split(' ')[0]} <span>"{SelectPlayerInfo.Pseudo}"</span> {SelectPlayerInfo.Name.split(' ')[1]}</h2>
                                <div className="RsIcons">
                                    {SelectPlayerInfo.Rs[0] !== "" &&
                                    <a href={SelectPlayerInfo.Rs[0]}>
                                        <img src="https://img.icons8.com/ios-glyphs/50/13A0B6/twitter.png" alt=""/>
                                    </a>}
                                    {SelectPlayerInfo.Rs[1] !== "" &&
                                    <a href={SelectPlayerInfo.Rs[1]}>
                                        <img src="https://img.icons8.com/material-outlined/24/13A0B6/twitch.png" alt=""/>
                                    </a>}
                                    {SelectPlayerInfo.Rs[2] !== "" &&
                                    <a href={SelectPlayerInfo.Rs[2]}>
                                        <img src="https://img.icons8.com/material-outlined/48/13A0B6/instagram-new--v1.png" alt=""/>
                                    </a>}
                                </div>
                                <img className="Signature" src={lienBck  + `/Image/Team/${"SakenSignature"}`} alt=""/>
                            </div>
                            <div className="PlayerInfo">
                                <h3>{'<role>'+ SelectPlayerInfo.Role + '</role>'}</h3>
                                <h3>{'<paie>'+ SelectPlayerInfo.Countrie +'</paie>'}</h3>
                                <h3>{'<age>'+ SelectPlayerInfo.Age +'</age>'}</h3>
                            </div>
                        </div>
                        {PlayerPageType === true &&
                        <div className="VideoContain">
                            <h1 className="TitleSection"><span>His</span> Play</h1>
                            <div className="VideoPlayer">
                                <i className="shadows shad1"></i>
                                <i className="shadows shad2"></i>
                                <i className="shadows shad3"></i>
                                <i className="shadows shad4"></i>
                                <i className="FullScreenIcon" onClick={()=> openFullscreen()} ><img src="https://img.icons8.com/material-outlined/24/FFFFFF/full-screen--v1.png" alt=""/></i>
                                <video muted preload="true" id="PlayerVideo">
                                    <source src='/Video/SAKEN.mp4' type="video/mp4"/>
                                </video>
                                <div className="ButtonVod">
                                    <img onClick={()=> document.getElementById("PlayerVideo") && ChangeVideo()} src={document.getElementById("PlayerVideo") !== null && (document.getElementById("PlayerVideo").paused) ? "https://img.icons8.com/ios-glyphs/90/FFFFFF/play.png" : "https://img.icons8.com/ios-glyphs/90/FFFFFF/pause--v1.png"} alt=""/>
                                    <img onClick={()=> resetVod()} src="https://img.icons8.com/material-outlined/96/FFFFFF/restart--v2.png" alt=""/>
                                </div>
                            </div>
                            <div className="LinkToOtherPagePlayer">
                                <a href="https://karmineshop.com/products/maillot-karmine-corp-summer-split-2021"><h1><img src="https://img.icons8.com/small/32/446186/left.png" alt=""/> <span>Its</span> Jersey</h1></a>
                                <h1  onClick={()=>setPlayerPageType(!PlayerPageType)}><span>His</span> Legacy <img src="https://img.icons8.com/small/32/446186/right.png" alt=""/></h1>
                            </div>
                        </div>
                        } 
                        {PlayerPageType === false &&
                        <div className="PalmaresPlayerContain">
                            <h1 className="TitleSection"><span>His</span> Legacy</h1>
                            <p>Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur. Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur. Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur.</p>
                            <h1 className="citationReverse"><span>"</span>La <span>Karmine</span>, c’est l’équipe du peuple. C’est comme un club de foot : on <span>gagne</span> ensemble, et quand on perd, on pleure <span>ensemble</span>.<span>"</span></h1>
                            <p>Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur.Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur. Incididunt pariatur Lorem in veniam ex mollit. Sunt ullamco ipsum et cupidatat. Quis eu irure consectetur incididunt est aliqua consectetur id officia in esse est et consectetur.</p>
                            <h1 className="citation"><span>"</span>La <span>Karmine</span>, c’est l’équipe du peuple. C’est comme un club de foot : on <span>gagne</span> ensemble, et quand on perd, on pleure <span>ensemble</span>.<span>"</span></h1>
                            <div className="LinkToOtherPagePlayer">
                                <h1  onClick={()=>setPlayerPageType(!PlayerPageType)}><img src="https://img.icons8.com/small/32/446186/left.png" alt=""/><span>His</span> Play</h1>
                                <a href="https://karmineshop.com/products/maillot-karmine-corp-summer-split-2021"><h1><span>Its</span> Jersey<img src="https://img.icons8.com/small/32/446186/right.png" alt=""/></h1></a>
                            </div>
                        </div>
                        }
                    </div> 
                </div> 
            </div>

            }
            <Footer/>
        </>
    )}
    // Hook
    function useWindowSize() {
        // Initialize state with undefined width/height so server and client renders match
        // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
        const [windowSize, setWindowSize] = useState({
        width: undefined,
        height: undefined,
        });
        useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
            // Set window width/height to state
            setWindowSize({
            width: window.innerWidth,
            height: window.innerHeight,
            });
        }
        // Add event listener
        window.addEventListener("resize", handleResize);
        // Call handler right away so state gets updated with initial window size
        handleResize();
        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
        }, []); // Empty array ensures that effect is only run on mount
        return windowSize;
    }

export default GamePage

