import Headers from '../Components/MainPage/Headers'
import Footer from '../Components/MainPage/Footer'
import '../Css/PageCss/TeamPage.css'
import {Link} from 'react-router-dom'

const TeamsPager = ({lienBck, ArraySponso}) => {
    return (
        <>
            <Headers Page={'T'} lienBck={lienBck} ArraySponso={ArraySponso}/>
            <div className="TeamChoose">
                <div className="Lol">
                    <Link to='/Team/Lol' onClick={()=>window.location.href="/Team/Lol"} className="LinkPageTeam">
                        <h1 className="TitleJeux">League of Legend</h1>
                    </Link>
                </div>
                <div className="Tft">
                    <Link to='/Team/Tft' onClick={()=>window.location.href="/Team/Tft"} className="LinkPageTeam">
                        <h1 className="TitleJeux">Teams figth taticts</h1>
                    </Link>
                </div>
                <div className="Tm">
                    <Link to='/Team/Tm' onClick={()=>window.location.href="/Team/Tm"} className="LinkPageTeam">
                        <h1 className="TitleJeux">Trackmania</h1>
                    </Link>
                </div>
                <div className="Rl">
                    <Link to='/Team/Rl' onClick={()=>window.location.href="/Team/Rl"} className="LinkPageTeam">
                        <h1 className="TitleJeux">Rocket League</h1>                        
                    </Link>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default TeamsPager
