import '../../Css/Carte.css'
import LogoCarte from '../../Image/Team/Lol/LogoCarte.png'
import FigureBack from '../../Image/Team/Lol/Figureteste-removebg-preview.png'
import FigureCard from '../../Image/Team/Lol/FigureCard.png'

const Carte = ({ImgPlayer, LogoRole, Pseudo, setPlayerFocus, PlayerFocus, ShowTextCArdNotSelect, NumPlayer}) => {
    return (
        <div className="flip-card" onClick={()=> {setPlayerFocus(Pseudo); ShowTextCArdNotSelect(false);} }>
            <div className="flip-card-inner Center ContainerAllPlayer">
                <div className="flip-card-front" style={{boxShadow: PlayerFocus === Pseudo &&  "0px 0px 23px 5px  rgba(49,117,188,1)"}}>
                    <h3 className="NumberCard" style={{top :"0", left:"1.5vh"}}>{NumPlayer}</h3>
                    <img className="ImgPlayer" src={ImgPlayer} alt=""/>
                    <img className="LogoRole" src={LogoRole} alt=""/>
                    <img className="LogoCarte" src={LogoCarte} alt=""/>
                    <h3 className="NumberCard ReverseNumber" style={{transform :"rotate(-180deg)",bottom :"0", right:"1.5vh"}}>{NumPlayer}</h3>
                </div>
                <div className="flip-card-back" style={{boxShadow: PlayerFocus === Pseudo &&  "0px 0px 23px 5px  rgba(49,117,188,1)"}}>
                    <img className="FigureCArd" src={FigureCard} alt=""/>
                    <h1 className="TextBackCard">{Pseudo}</h1>
                    <img className="FigureBack" src={FigureBack} alt=""/>
                </div>
            </div>
        </div>
    )
}

export default Carte
