
import Facebook from '../Image/Footer/FacebookLogo.png'
import Instagram from '../Image/Footer/InstagrameLogo.png'
import Twitter from '../Image/Footer/twitter.png'
import Youtube from '../Image/Footer/YoutubeLogo.png'
const RsComponent = ({lienTwitter, lienFacebook, lienInstagrame, lienYoutube}) => {
    return (
        <>
            <img alt="" src={Facebook} width={"36px"} height={"42px"}/>
            <img alt="" src={Instagram} width={"40px"} height={"32px"}/>
            <img alt="" src={Twitter} width={"42px"} height={"38px"}/>
            <img alt="" src={Youtube} width={"50px"} height={"26px"}/>   
        </>
    )
}

export default RsComponent
