import '../../Css/Footer.css'
import emote from '../../Image/Footer/emote.png'
import Logo from '../../Image/Footer/logo.png'
import RsComponent from '../RsComponent'
import { Link } from 'react-router-dom'

const Footer = () => {
    return (
        <div className="Footer">
            <img src={Logo} alt=""  style={{width:"auto", height : "auto", gridColumn : "2"}}/>
            <div className="Colonne1">
                <h1>Karmine Corp</h1>
                <h2>Communoté</h2>
                <h2>Brand</h2>
                <Link to="/Contact"><h2>Contact</h2></Link>
            </div>
            <div className="Colonne2">
                <div style={{display : "flex", alignItems : "baseline"}}>
                    <h1>Rejoigner les <span>Ultras</span></h1>
                    <img alt="" src={emote} style={{transform:"rotate(30deg)", width:"auto"}}/>
                </div>
                <h1><span>#</span>KCORP</h1>
                <div className="Colonne3">
                    <RsComponent/>
                </div>
            </div>
        </div>
    )
}
export default Footer
