import {useState} from 'react'
import UseAutocomplete from '../InputAutoComplite'


const MatchHistorie = () => {
    const [ShowRecherche, setShowRecherche] = useState(false);

    const ListeTournois = [
        {title:'OGL'},
        {title:'LFL'},
        {title:'EUM'},
        {title:'kennen cup'}
    ]

    const ListeAdversaire = [
        {title:'Solanul'},
        {title:'Solanul'},
        {title:'Solanul'},
        {title:'Solanul'},
        {title:'Solanul'},
        {title:'Solanul'},
    ]

    const ListeJeux = [
        {title:'League of Legends'},
        {title:'TrackMania'},
        {title:'Teamfight Tactics'}
    ]

    const ListeDay = [
        { title :'1'},{ title :'2'},{ title :'3'},{ title :'4'},{ title :'5'},{ title :'6'},{ title :'7'},{ title :'8'},{ title :'9'},{ title :'10'},{ title :'11'},{ title :'12'},{ title :'13'},{ title :'14'},{ title :'15'},{ title :'16'},{ title :'17'},{ title :'18'},{ title :'19'},{ title :'20'},{ title :'21'},{ title :'22'},{ title :'23'},{ title :'24'},{ title :'25'},{ title :'26'},{ title :'27'},{ title :'28'},{ title :'29'},{ title :'30'},{ title :'31'}
        // { title :1},{ title :2},{ title :3},{ title :4},{ title :5},{ title :6},{ title :7},{ title :8},{ title :9},{ title :10},{ title :11},{ title :12},{ title :13},{ title :14},{ title :15},{ title :16},{ title :17},{ title :18},{ title :19},{ title :20},{ title :21},{ title :22},{ title :23},{ title :24},{ title :25},{ title :26},{ title :27},{ title :28},{ title :29},{ title :30},{title : 31}
    ]

    const ListeMonth = [
        { title :'1'},{ title :'2'},{ title :'3'},{ title :'4'},{ title :'5'},{ title :'6'},{ title :'7'},{ title :'8'},{ title :'9'},{ title :'10'},{ title :'11'},{ title :'12'}
    ]

    const ListeYears = [
        {title :"2020"},{title :"2021"} 
    ]

    return (
        <>
            <div className="RechercheMatch">
                <div onClick={()=> setShowRecherche(!ShowRecherche)}><h1 className="Text ButtonMatchFind" style={{borderRadius : ShowRecherche && "10px 10px 0 0", borderBottom : ShowRecherche && "none"}} >Recherche<span style={{writingMode : ShowRecherche && "vertical-rl"}}>{">"}</span></h1></div>
                {ShowRecherche &&
                    <div className="FormContain" >
                        <div className="Lane1">
                            <UseAutocomplete id="InputAdversaire" listbox={"Autocomplite"} InputContain={"InputAdversaire"} Data={ListeAdversaire} PlaceHolder={"Adversaire"} />
                            <UseAutocomplete listbox={"Autocomplite"} InputContain={"InputJeux"} Data={ListeJeux} PlaceHolder={"Jeux"} />
                        </div>
                        <div className="Lane2">
                            <div className="Date" >
                                <UseAutocomplete listbox={"Autocomplite"} InputContain={"DateInput"} PlaceHolder={"Jour"} Data={ListeDay}/>
                                <h2>/</h2>
                                <UseAutocomplete listbox={"Autocomplite"} InputContain={"DateInput"} PlaceHolder={"Mois"} Data={ListeMonth}/>
                                <h2 style={{color : "#12DCFC"}}>/</h2>
                                <UseAutocomplete listbox={"Autocomplite"} InputContain={"DateInput"} PlaceHolder={"Année"} Data={ListeYears}/>
                            </div>
                            <UseAutocomplete listbox={"Autocomplite"} InputContain={"InputTournois"} Data={ListeTournois} PlaceHolder={"Tournois"} />
                        </div>
                        <button className="ButtonRecherche" >Recherche</button>
                    </div>
                }
            </div>
        </>
    )
}

export default MatchHistorie