import {useState} from 'react'
import '../../Css/Carousel.css'
const Carousel = () => {
    const [CarouselShow, setCarouselShow] = useState(0);

    const Image = [{name: "Msi Sponso",source : "https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/1/KARMINE_CORP_x_MSI.jpg"}, {name: "Lfl Kc", source : "https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/2/KARMINE_CORP_REJOINS_LA_LFL.jpg"}, {name: "Kc Chupa Chups", source : "https://dtlaca8pgqtx.cloudfront.net/uploads/attachment/file/3/ChupaChupsnouveausponsorRS.jpg"}];

    function Modulo(a,b){
        return (a%b + b)%b;
    }

    return (
        <div className="CarouselContaine Colone reveal revealLeft">
            <div className="Carousel Colone" >
                <img className="ImageNews" src={Image[Modulo(CarouselShow, 3)].source} alt={Image[Modulo(CarouselShow, 3)].name} ></img>
                <div className="ArrowContainer CenterVertically">
                    <p onClick={()=>{setCarouselShow(CarouselShow - 1)}} className="Arrow CenterVertically" >ᐸ</p>
                    <p onClick={()=>{setCarouselShow(CarouselShow + 1);}} className="Arrow CenterVertically" >ᐳ</p>
                </div>
            </div>
            <div className="CarouselShow" >
                <div className="CarouselBtn" style={{backgroundColor : Modulo(CarouselShow, 3) === 0 && "#ffffffff" }} onClick={()=>{setCarouselShow(0)}} ></div>
                <div className="CarouselBtn" style={{backgroundColor : Modulo(CarouselShow, 3) === 1 && "#ffffffff" }} onClick={()=>{setCarouselShow(1)}} ></div>
                <div className="CarouselBtn" style={{backgroundColor : Modulo(CarouselShow, 3) === 2 && "#ffffffff" }} onClick={()=>{setCarouselShow(2)}} ></div>
            </div>
            <div className="TextLine Line2"></div>
        </div>
    )
}

export default Carousel
