import {useState} from 'react'
import {Link} from "react-router-dom";

const NavBar = () => {
    const [ShowTeams, setShowTeams] = useState(false);
    const [ShowKarmine, setShowKarmine] = useState(false);

    return (
        <div className="NavBar">
            <div className="ListePage">
                <div className="BcKTExt" ><Link to="/News" ><h1><span>N</span>ews</h1></Link></div>
                <div className="BcKTExt"> <Link to="/Match" > <h1><span>M</span>atch</h1></Link> </div>
                <div className="ListeInnerText" style={{margin:"0"}}>
                    <div className="BcKTExt" onClick={()=> setShowTeams(!ShowTeams)}> <h1><span>T</span>eams</h1> </div>
                    {ShowTeams && 
                    <ul>
                        <li><Link to="/Team/Lol" onClick={()=>window.location.href="/Team/Lol"}><h2 id="LeagueTExt" style={{color:"#FCC967"}}>League of legends</h2></Link></li>
                        <li><Link to="/Team/Tft" onClick={()=>window.location.href="/Team/Tft"}><h2 id="TftTExt" style={{color:"#0D5065"}}>Team fight tactics</h2></Link></li>
                        <li><Link to="/Team/Tm" onClick={()=>window.location.href="/Team/Tm"}><h2 id="TmTExt" style={{color:"#0FE689"}}>Trackmania</h2></Link></li>
                        <li><Link to="/Team/Rl" onClick={()=>window.location.href="/Team/Rl"}><h2 id="RlText" style={{color:"#009BF2"}}>Rocket League</h2></Link></li>
                    </ul>}
                </div>

                <div className="BcKTExt" > <a rel="noreferrer" target="_blank" href="https://karmineshop.com/" ><h1><span>S</span>hop</h1></a> </div>
                <div className="ListeInnerText" style={{margin:"0"}}>
                    <div className="BcKTExt" onClick={()=> setShowKarmine(!ShowKarmine)}> <h1><span>K</span>armine</h1> </div>
                    {ShowKarmine && 
                    <ul>
                        <li><Link to="/About/#About"><h2><span>A</span>bout</h2></Link></li>
                        <li><Link to="/About/#Sponsort"><h2><span>S</span>ponsort</h2></Link></li>
                        <li><Link to="/About/#Partenaire"><h2><span>P</span>artenaire</h2></Link></li>
                        <li><h2 style={{opacity :'.35'}}><span>P</span>almares</h2></li>
                    </ul>}
                </div>
            </div>
        </div>
    )
}

export default NavBar
