import Headers from '../Components/MainPage/Headers'
import About from '../Components/MainPage/About'
import News from '../Components/MainPage/News'
import Matchs from '../Components/MainPage/Matchs'
import Shop from '../Components/MainPage/Shop'
import Footer from '../Components/MainPage/Footer'
import {useState , useEffect} from 'react'



const Main = ({lienBck, ArraySponso, ArrayText, PastMatch, FutureMatch, ArrayImg}) => {
    const [Scroll, setScroll] = useState(true);

    /*Animation on scroll with that*/ 
    function scrollF(){
        if (window.scrollY > 0){
            setScroll(false);
        }
        else{
            setScroll(true);
        };
    };
    window.addEventListener('scroll', scrollF)
    const [options] = useState({
        root: null,
        rootMargin: '0px',
        threshold : .1
    });
    const handleIntersect = function (entries, observer) {
        entries.forEach(function (entry) {
            if (entry.intersectionRatio > .1) {
                if (entry.target.classList.contains('revealLeft')){
                    entry.target.classList.remove('revealLeft')
                    entry.target.classList.add('transformLeft')
                }
                else if (entry.target.classList.contains('revealRight')){
                    entry.target.classList.remove('revealRight')
                    entry.target.classList.add('transformRight')
                }
                else if (entry.target.classList.contains('revealDroit')){
                    entry.target.classList.remove('revealDroit')
                    entry.target.classList.add('transformDroit')
                }
                else if (entry.target.classList.contains('revealGauche')){
                    entry.target.classList.remove('revealGauche')
                    entry.target.classList.add('transformGauche')
                }
                else if (entry.target.classList.contains('WritingAnime')){
                    entry.target.classList.remove('WritingAnime')
                    entry.target.classList.add('typewriter-text')
                }
                entry.target.classList.remove('reveal')
                observer.unobserve(entry.target)
            }
        })
    };

    useEffect(() => {
        const observer = new IntersectionObserver(handleIntersect, options)
        const targets = document.querySelectorAll('.reveal')
        targets.forEach(function (target) {
            observer.observe(target)
        })
    }, [options])

    // window.addEventListener("DOMContentLoaded", function () {
    //     const observer = new IntersectionObserver(handleIntersect, options)
    //     const targets = document.querySelectorAll('.reveal')
    //     targets.forEach(function (target) {
    //         observer.observe(target)
    //     })
    // });
    return (
        <>
            <Headers Scroll={Scroll ? true : false } lienBck={lienBck} ArraySponso={ArraySponso}/>
            <About Text={ArrayText.find(element => element.Conponent === "Main") !== undefined && ArrayText.find(element => element.Conponent === "Main").Text}/>
            <div className="StyleComponentMain">
                <div className="BarreScrollStyle">
                    <span>Scroll</span>
                    <i></i>
                </div>
                <div className="TwitchContainAbout">
                    <div className="DivContainTwitch">
                        <h1>twitch.tv/<span>kamet0</span></h1>
                    </div>
                    <h1 className="reveal revealDroit">RP #KCORP #KCORP #KCORP #KCORP</h1>
                    <h1  className="reveal revealGauche">CORPWIN #KCORPWIN #KCORPWIN #KCORPWIN</h1>
                    <h1  className="reveal revealDroit" style={{marginBottom: "0"}}>ORP #KCORP #KCORP #KCORP #KCORP</h1>
                </div>
            </div>
            <News Text={ArrayText.find(element => element.Conponent === "News") !== undefined && ArrayText.find(element => element.Conponent === "News").Text}/>
            <Matchs lienBck={lienBck} PastMatch={PastMatch} FutureMatch={FutureMatch}/>
            <Shop ArrayImg={ArrayImg} lienBck={lienBck} Text={ArrayText.find(element => element.Conponent === "Shop") !== undefined && ArrayText.find(element => element.Conponent === "Shop").Text}/>
            <Footer/>
        </>
    )
}

export default Main
